* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: linear-gradient(135deg, #0f2027 0%, #203a43 50%, #2c5364 100%);
  min-height: 100vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  margin-bottom: 30px;
  padding: 20px 0;
}

h1 {
  text-shadow: 2px 2px 4px #00000080;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(45deg, #c89b3c, #f0e6d2) text;
  margin-bottom: 10px;
  font-size: 3rem;
}

.subtitle {
  color: #c9aa71;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.search-container {
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
  display: -ms-flexbox;
  display: flex;
}

#searchInput {
  color: #fff;
  background: #ffffff1a;
  border: 2px solid #c89b3c;
  border-radius: 25px;
  outline: none;
  width: 300px;
  padding: 12px 20px;
  font-size: 16px;
  transition: all .3s;
}

#searchInput::-moz-placeholder {
  color: #c9aa71;
}

#searchInput::placeholder {
  color: #c9aa71;
}

#searchInput:focus {
  border-color: #f0e6d2;
  box-shadow: 0 0 15px #c89b3c80;
}

.filter-buttons {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
  display: -ms-flexbox;
  display: flex;
}

.filter-btn {
  color: #f0e6d2;
  cursor: pointer;
  background: #c89b3c33;
  border: 2px solid #c89b3c;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 14px;
  transition: all .3s;
}

.filter-btn:hover, .filter-btn.active {
  color: #0f2027;
  background: #c89b3c;
  transform: translateY(-2px);
}

.loading {
  text-align: center;
  color: #c89b3c;
  padding: 50px;
  font-size: 1.5rem;
}

.error {
  text-align: center;
  color: #ff6b6b;
  background: #ff6b6b1a;
  border: 1px solid #ff6b6b;
  border-radius: 10px;
  margin: 20px 0;
  padding: 20px;
  font-size: 1.2rem;
}

.champions-grid {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  padding: 20px 0;
  display: grid;
}

.champion-card {
  text-align: center;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff0d;
  border: 2px solid #c89b3c4d;
  border-radius: 15px;
  padding: 20px;
  transition: all .3s;
}

.champion-card:hover {
  border-color: #c89b3c;
  transform: translateY(-10px);
  box-shadow: 0 15px 30px #c89b3c4d;
}

.champion-image {
  object-fit: cover;
  border: 3px solid #c89b3c;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin: 0 auto 15px;
  transition: all .3s;
}

.champion-card:hover .champion-image {
  border-color: #f0e6d2;
  transform: scale(1.1);
}

.champion-name {
  color: #f0e6d2;
  margin-bottom: 8px;
  font-size: 1.5rem;
  font-weight: bold;
}

.champion-title {
  color: #c9aa71;
  margin-bottom: 10px;
  font-size: 1rem;
  font-style: italic;
}

.champion-tags {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  margin-bottom: 10px;
  display: -ms-flexbox;
  display: flex;
}

.tag {
  color: #f0e6d2;
  background: #c89b3c4d;
  border: 1px solid #c89b3c;
  border-radius: 12px;
  padding: 4px 8px;
  font-size: .8rem;
}

.champion-stats {
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  margin-top: 15px;
  font-size: .9rem;
  display: grid;
}

.stat {
  border-bottom: 1px solid #c89b3c33;
  justify-content: space-between;
  padding: 4px 0;
  display: -ms-flexbox;
  display: flex;
}

.stat-name {
  color: #c9aa71;
}

.stat-value {
  color: #f0e6d2;
  font-weight: bold;
}

.champion-description {
  color: #c9aa71;
  text-overflow: ellipsis;
  max-height: 60px;
  margin-top: 10px;
  font-size: .9rem;
  line-height: 1.4;
  overflow: hidden;
}

.champion-count {
  text-align: center;
  color: #c89b3c;
  margin-bottom: 20px;
  font-size: 1.1rem;
}

@media (width <= 768px) {
  .container {
    padding: 10px;
  }

  h1 {
    font-size: 2rem;
  }

  #searchInput {
    width: 100%;
    max-width: 300px;
  }

  .champions-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 15px;
  }

  .filter-buttons {
    gap: 5px;
  }

  .filter-btn {
    padding: 6px 12px;
    font-size: 12px;
  }
}
